*{
    background-color: rgb(84, 84, 245);

}

.main{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 600px;
}

.table{

  background-color: azure;
  width: 20%;
  height: 100px;
  text-align: center;

}
h1{
    text-align: center;
    color: lightgreen;
}
input{
    margin-top: 10px;
    background-color: azure;
}
.btn-sub{
 
    background-color: rgb(233, 233, 243);
    width: 50%;
  
}
.option{
    display: flex;
    justify-content: space-between;
}
@media screen and (min-width: 300px) {
  
    .table {
      width: 300px;
      border-radius: 20px;
    }
    .table input{
        width: 60%;
    }
    .btn-sub{
        cursor: pointer;
        background-color: brown;
    }
  }
